<template>
  <div class="mapTable" >
    <div class="btnBox" v-show="!spotTable" @click="spotTable=true">
      <i-button icon="ios-chatboxes"></i-button>
    </div>
    <div class="modelBox" v-if="spotTable">
      <p>
        <strong class="active"  @click="switchTab(0)">
          航点
        </strong>
      </p>
      <div class="table_box">
        <Table stripe :columns="columns1" :data="data1" size="small" v-show="telescopic" :height="tableHeight" highlight-row></Table>
        <Page :total="total" :page-size="pageSize" size="small" simple class="thePage" @on-change="pageChange" :current="pageNum" show-total></Page>
        <div class="register_box" :class="{regActive: registerTab}">
          <RouteSpotRegister :id="registerId" @closeReg="closeReg"></RouteSpotRegister>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getMarkList } from '@/api/mobilesSupervision'
import { cacuLonLat } from '@/utils/common'
import RouteSpotRegister from './RouteSpot_register'

export default {
  name: 'MapTable',
  data () {
    return {
      /* 表格高度 */
      tableHeight: window.innerHeight - 316,
      /* 列表框打开 */
      spotTable: true,
      /* 列表伸缩框打开 */
      telescopic: true,
      /* 登记表查询id */
      tabletId: '',
      /* 原始界桩表头 */
      columns1: [
        {
          title: '编号',
          key: 'bzId',
          width: 180,
          fixed: 'left',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  color: '#2d8cf0',
                  textDecoration: 'underline'
                },
                on: {
                  click: () => {
                    this.ondbclickTd(params.row.bzLng, params.row.bzLat, params.row.bzId)
                  }
                }
              }, params.row.bzId)
            ])
          }
        },
        {
          title: '名称',
          key: 'bzDidian',
          align: 'center',
          minWidth: 100
        },
        {
          title: '海拔',
          key: 'bzHaiba',
          align: 'center',
          minWidth: 100
        },
        {
          title: '经度',
          key: 'bzLng',
          align: 'center',
          minWidth: 150,
          render: (h, params) => {
            return h('span', cacuLonLat(params.row.bzLng))
          }
        },
        {
          title: '纬度',
          key: 'bzLat',
          align: 'center',
          minWidth: 150,
          render: (h, params) => {
            return h('span', cacuLonLat(params.row.bzLat))
          }
        },
        {
          title: '生境',
          key: 'bzShengjing',
          align: 'center',
          minWidth: 100
        },
        {
          title: '天气',
          key: 'bzTianqi',
          align: 'center',
          minWidth: 100
        },
        {
          title: '详情',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            return h('Button', {
              props: {
                size: 'small',
                type: 'primary'
              },
              on: {
                click: () => {
                  this.registerId = params.row.bzId
                  this.registerTab = true
                }
              }
            }, '详情')
          }
        }
      ],
      /* 表格数据 */
      data1: [],
      /* 页码 */
      pageNum: 1,
      /* 每页条数 */
      pageSize: Math.floor((window.innerHeight - 305) / 40) - 1,
      /* 总条数 */
      total: 0,
      /* 打开详情页 */
      registerTab: false,
      /* 详情页id */
      registerId: ''
    }
  },
  props: {
    obj: {},
    srld: null
  },
  components: {
    RouteSpotRegister
  },
  watch: {
    obj () {
      this.getTableData()
    },
    pageNum () {
      this.getTableData()
    },
    spotTable (val) {
      this.$emit('mapSizeChange', val)
    },
    srld (val) {
      this.pageNum = 1
      this.getTableData()
    }
  },
  created () {
    this.getTableData()
  },
  mounted () {
    // 窗口改变时图表自适应
    window.addEventListener('resize', () => {
      this.tableHeight = window.innerHeight - 327
      this.pageSize = Math.floor((window.innerHeight - 305) / 40) - 1
      this.getTableData()
    }, false)
  },
  methods: {
    /* 获取拐点坐标列表 */
    getTableData () {
      getMarkList({
        data: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          bzShengjing: this.obj.code
        }
      }).then((res) => {
        this.data1 = res.data.data.rows
        this.total = res.data.data.total
        this.$emit('getPointData', res.data.data.rows)
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 双击控制地图跳转经纬度 */
    ondbclickTd (lon, lat, id) {
      this.$emit('ondbclickDingwei', lon, lat, id)
    },
    /* 分页跳转 */
    pageChange (e) {
      this.pageNum = e
    },
    /* 日期格式转换 */
    renderTime (date) {
      let d = new Date(date)
      let times = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate()
      return times
    },
    /*  */
    closeReg () {
      this.registerTab = false
    }
  }
}
</script>

<style lang="scss" scoped rel="stylesheet/scss">
  .mapTable{
    height:100%
  }
  .btnBox{
    position: absolute;right:10px;top:10px;
  }
  .modelBox{
    background: #F5F5F5;
    height:100%;
    margin-right: -1px;
    p{
      padding:0 15px 0 0;
      position: relative;
      overflow: hidden;
      text-align: left;
      border: 1px #d1d1d1 solid;
      &:before{
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height:0;
      }
      strong{
        float: left;
        display: inline-block;
        cursor: pointer;
        height: 100%;
        font-size:14px;
        padding:5px 20px;
      }
      .active{
        border: 1px solid #DADCE0;
        background: #fff;
        border-bottom: 0;
      }
      span{
        float: right;
        cursor: pointer;
      }
    }
    .table_box{
      padding:0 0 45px;
      .thePage{
        position: absolute;
        bottom:10px;
        right:10px;
      }
    }
    .register_box{
      position: absolute;
      top: 34px;
      left: calc(100% + 20px);
      width: calc(100% - 6px);
      height: calc(100% - 34px);
      background: #fff;
      z-index: 150;
      transition: all 0.8s ease;
      overflow: auto;
    }
    .regActive {
      top: 34px;
      left: 6px;
    }
  }
</style>
