<template>
  <div id="tabletRegister">
    <Button @click="$emit('closeReg')" type="primary" size="small" style="position: absolute;right: 10px;top:8px">返回</Button>
    <h2>航点信息详情</h2>
    <table border='0' cellpadding='0' cellspacing='0' class="tab">
      <tr>
        <td colspan='6' style="width: 25%">航点名称</td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{data.bzDidian}}</td>
        <td colspan='6' style="width: 25%">天气情况</td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{data.bzTianqi}}</td>
      </tr>
      <tr>
        <td colspan='6' style="width: 25%">人类活动</td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{data.bzRenlei}}</td>
        <td colspan='6' style="width: 25%">生境</td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{data.bzShengjing}}</td>
      </tr>
      <tr id="images">
        <td colspan='6'>照片</td>
        <td colspan='18' style="font-weight: normal;text-align: left">
          <div  v-for="(item, index) in data.list" :key="index" v-show="getSuffixName(item.phoUrl) !== 'amr'"
                style="display: inline-block;height: 100px;margin-left: 5px">
              <img :src="item.phoUrl" alt="" @click="imgEnlarges()" v-if="getSuffixName(item.phoUrl) !== 'amr'"
                   style="height:100%;max-width:200px;border: 1px #d1d1d1 solid;min-width: 60px">
          </div>
        </td>
      </tr>
      <tr>
        <td colspan='6'>录音</td>
        <td colspan='18' style="font-weight: normal;text-align: left">
          <div  v-for="(item, index) in data.list" :key="index" v-show="getSuffixName(item.phoUrl) === 'amr'">
            <p style="padding: 3px">{{item.phoUrl.split('/')[item.phoUrl.split('/').length-1]}}
              <a :href="item.phoUrl"><Button size="small" type="primary" style="margin-left: 5px">下载</Button></a>
            </p>
          </div>
        </td>
      </tr>
      <tr>
        <td colspan='6'>备注</td>
        <td colspan='18' style="font-weight: normal;text-align: left">{{data.bzBeizhu}}</td>
      </tr>
    </table>
    <h4>问题核查处理情况表</h4>
    <table border='0' cellpadding='0' cellspacing='0' class="tab">
      <tr>
        <td colspan='6'>问题</td>
        <td colspan='18' style="font-weight: normal">{{data.tquestionVerification.tqvQuestion}}</td>
      </tr>
      <tr>
        <td colspan='6'>编号</td>
        <td colspan='18' style="font-weight: normal">{{data.tquestionVerification.tqvSerivalNumber}}</td>
      </tr>
      <tr>
        <td colspan='24'>监测情况</td>
      </tr>
      <tr>
        <td colspan='6' style="width: 25%">活动/设施名称</td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{data.tquestionVerification.tqvFacilityName}}</td>
        <td colspan='6' style="width: 25%">活动/设施类型</td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{data.tquestionVerification.tqvFacilityType}}</td>
      </tr>
      <tr>
        <td colspan='6'>所在功能区</td>
        <td colspan='18' style="font-weight: normal">{{data.tquestionVerification.tqvSectorization}}</td>
      </tr>
      <tr>
        <td colspan='6' style="width: 25%">面积</td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{data.tquestionVerification.tqvArea}}</td>
        <td colspan='6' style="width: 25%">变化类型</td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{data.tquestionVerification.tqvChangeType}}</td>
      </tr>
      <tr>
        <td colspan='24'>核查情况</td>
      </tr>
      <tr>
        <td colspan='6'>活动/设施现状</td>
        <td colspan='18' style="font-weight: normal">{{data.tquestionVerification.tqvFacilityStatus}}</td>
      </tr>
      <tr>
        <td colspan='6' style="width: 25%">建设时间</td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{renderTime(data.tquestionVerification.tqvBuildTime)}}</td>
        <td colspan='6' style="width: 25%">有无环评手续</td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{data.tquestionVerification.tqvFormalities}}</td>
      </tr>
      <tr>
        <td colspan='6'>存在问题及主要生态环境影响</td>
        <td colspan='18' style="font-weight: normal">{{data.tquestionVerification.tqvInfluence}}</td>
      </tr>
      <tr>
        <td colspan='6'>处理情况</td>
        <td colspan='18' style="font-weight: normal">{{data.tquestionVerification.tqvResult}}</td>
      </tr>
      <tr>
        <td colspan='6' style="width: 25%">核查单位</td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{data.tquestionVerification.tqvCheckUnit}}</td>
        <td colspan='6' style="width: 25%">联系方式</td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{data.tquestionVerification.tqvContactWay}}</td>
      </tr>
      <tr>
        <td colspan='6' style="width: 25%">核查时间</td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{renderTime(data.tquestionVerification.tqvCheckTime)}}</td>
        <td colspan='6' style="width: 25%">填表人</td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{data.tquestionVerification.tqvPreparer}}</td>
      </tr>
      <tr>
        <td colspan='6' style="width: 25%">核查人</td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{data.tquestionVerification.tqvCheckPeople}}</td>
        <td colspan='6' style="width: 25%">审核人</td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{data.tquestionVerification.tqvVerifier}}</td>
      </tr>
    </table>
  </div>
</template>

<script>
import { getMarkDetailById } from '@/api/mobilesSupervision'
import { cacuLonLat, renderTime, getSuffixName } from '@/utils/common'
import Viewer from 'viewerjs'
import 'viewerjs/dist/viewer.css'

export default {
  name: 'TabletRegister',
  data () {
    return {
      /* 获取的数据 */
      dataSourse: {
        tquestionVerification: {}
      },
      viewer: {}, // viewerjs实例化对象,
      viewers: {} // viewerjs实例化对象
    }
  },
  computed: {
    data: function () {
      let datas = JSON.parse(JSON.stringify(this.dataSourse))
      if (datas.tquestionVerification === null) {
        datas.tquestionVerification = {}
      }
      return datas
    }
  },
  props: {
    id: {}
  },
  watch: {
    id () {
      this.getMarkDetailById()
    }
  },
  mounted () {
    /* this.viewer = new Viewer(document.getElementById('image')) */
    this.viewers = new Viewer(document.getElementById('images'))
  },
  methods: {
    /* 获取原始界桩列表 */
    getMarkDetailById () {
      getMarkDetailById({
        data: {
          markId: this.id
        }
      }).then((res) => {
        this.dataSourse = res.data.data
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 图片放大显示 */
    imgEnlarge () {
      this.viewer.update()
    },
    /* 图片放大显示 */
    imgEnlarges () {
      this.viewers.update()
    },
    /* 日期格式转换 */
    renderTime (date) {
      return renderTime(date)
    },
    /* 日期格式转换 */
    getSuffixName (str) {
      return getSuffixName(str)
    },
    /* 经纬度转换为度分秒 */
    cacuLonLat (e) {
      return cacuLonLat(e)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  h2{
    padding:5px 0 0;
    font-weight: normal
  }
  .tab{
    border-collapse: collapse;
    table-layout: fixed;
    text-align: center;
    width: calc(100% - 16px);
    margin: 8px;
    border:solid #d1d1d1; border-width:1px 0 0 1px;
    td{
      padding: 5px;
      font-size: 12px;
      font-weight: bolder;
      border:solid #d1d1d1;
      border-width:0 1px 1px 0;
    }
  }
</style>
